// React/Redux
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { loadUsers } from '../actions/UserActions.js';
import { loadPosts, likeUnkikePost, addPost, removePost } from '../actions/PostActions.js';
import { addComment } from '../actions/CommentActions';
// import UserService from '../services/UserService';
import ScrollToTop from '../cmps/ScrollToTop'

// Style:
import '../../src/assets/css/index/index.css';
import '../../src/assets/css/Home/Home.css';
import '../../src/assets/css/Nav/Nav.css';

// Components
import Feed from '../cmps/Feed'
import PostAdd from '../cmps/PostAdd'
var randomUser = require('../assets/img/users/randomUser.png');

class Home extends Component {
  state = {
    user: '',
    isAppeared: false
  }
  async componentDidMount() {
    var scrollComponent = this;
    document.addEventListener("scroll", function (e) {
      scrollComponent.toggleNav();
    });

    this.props.loadUsers('home');
    this.props.loadPosts();

    const guest = {
      _id: '5e5796c01c9d44000049191b',
      username: 'guest',
      imgUrl: randomUser
    }

    this.setState({
      user: (this.props.loggedInUser) ? this.props.loggedInUser
        : guest
      // : await UserService.getById('5e5796c01c9d44000049191b')
    })
  }

  toggleNav() {
    var nav = document.querySelector('.nav')
    if (window.pageYOffset >= 100) {
      nav.classList.add('nav-opaque');
    }
    else if (window.pageYOffset <= 100) {
      nav.classList.remove('nav-opaque');
    }
    if (window.pageYOffset > 700) {
      this.setState({
        isAppeared: true
      }); nav.classList.add('nav-folded');
    }
    if (nav !== null && this.state.isAppeared === true && window.pageYOffset >= 700) {
      this.setState({
        isAppeared: true
      }); nav.classList.add('nav-folded');
    }
    else if (window.pageYOffset <= 700 && this.state.isAppeared === true) {
      if (nav !== null) {
        this.setState({
          isAppeared: false
        }); nav.classList.remove('nav-folded');
      }
    }
  }

  render() {
    const { loggedInUser, history, users, posts, comments, removePost, addComment, likeUnkikePost } = this.props
    const { user } = this.state

    return (
      <div className="home">

        <div className="home-container">
          <Feed loggedInUser={loggedInUser} user={user} history={history} users={users} posts={posts} comments={comments} removePost={removePost} addComment={addComment} likeUnkikePost={likeUnkikePost} />

          {this.props.users &&
            // {this.props.users && loggedInUser &&
            <section className="extras-container">

              <div className="user-image-container">
                {loggedInUser && <Link to={location => ({
                  ...location,
                  pathname: `/user/logged/${loggedInUser._id}`,
                  state: {
                    // loggedInUser: loggedInUser,
                    posts: posts,
                    users: users
                  }
                })}>
                  <img className="user-image" alt="user page" src={loggedInUser.imgUrl}></img>
                </Link>}
                {!loggedInUser && <img className="user-image" alt="user page" src={user.imgUrl}></img>}
                <span className="loggedin-user-nickname">{user.username}</span>
              </div>
              <ScrollToTop className="scroll-to-top" />
              <div className="extras">
                <PostAdd addPost={this.props.addPost} loggedInUser={loggedInUser} user={user} props={this.props} />
              </div>
            </section>}
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    users: state.user.users,
    loggedInUser: state.user.loggedInUser,
    posts: state.post.posts
  };
};
const mapDispatchToProps = {
  loadUsers,
  loadPosts, likeUnkikePost, addPost, removePost,
  addComment
};

export default connect(mapStateToProps, mapDispatchToProps)(Home);
